import React, { useState } from "react";
import Input from "../../../components/common/input/Input";
import Button from "../../../components/common/button/Button";
import SelectBox from "../../../components/common/selectBox/SelectBox";
import sampleImg from "../../../assets/admin_sample_img.svg";
import addCocktailImg from "../../../assets/icon/icon_add_cocktail_button.svg";
import { styled } from "styled-components";
import { handleChangeInput, handleChangeInputNumber } from "./handler";
import { BAR_INFO, ButtonOptions } from "./ManageInfoOptions";
import { CocktailItem } from "../../../components/admin/management/CocktailItem";
import { CocktailProps } from "../../../libs/interface/interfaceCocktail";
import { FORM_EVENT, INPUT_EVENT } from "../../../libs/interface/typeEvent";

export const ManageInfo = () => {
  //  
  const [barName, setBarName] = useState("");
  const [barLocation, setBarLocation] = useState("");
  const [barLocationDetail, setBarLocationDetail] = useState("");
  const [barMood, setBarMood] = useState("");
  const [activeCoverCharge, setActiveCoverCharge] = useState("");
  const [coverCharge, setCoverCharge] = useState("");
  const [activeDiscount, setActiveDiscount] = useState("");
  const [discount, setDiscount] = useState("");
  const [barOpeningTime, setBarOpeningTime] = useState("");
  const [barDetail, setBarDetail] = useState("");

  //   
  const [barPics, setBarPics] = useState<File[]>([]);
  const [previewImageList, setPreviewImageList] = useState<string[]>([]);

  //   
  const [cocktailList, setCocktailList] = useState<string[]>([]);
  const [showList, setShowList] = useState<string[]>([]);

  const handleSubmit = (e: FORM_EVENT, inputValues: any[]) => {
    const { ...data } = inputValues;

    console.log(data);
  };

  const handleBarPics = (e: INPUT_EVENT) => {
    const selectImage = e.target.files!;

    //  
    const selectPreviewImages = [...previewImageList];

    for (let i = 0; i < selectImage.length; i++) {
      let selectImageUrl = URL.createObjectURL(selectImage[i]);
      selectPreviewImages.push(selectImageUrl);
    }

    setBarPics([...Array.from(barPics), ...Array.from(selectImage)].slice(0, 4));
    setPreviewImageList(selectPreviewImages.slice(0, 4));
  };

  const deletePreviewImage = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    const deleteItemIdx = parseInt(e.currentTarget.value);
    setBarPics(barPics.filter((_, idx) => idx !== deleteItemIdx));
    setPreviewImageList(previewImageList.filter((_, idx) => idx !== deleteItemIdx));
  };

  const sampleCocktails: { info: CocktailProps }[] = [
    {
      info: { cocktailPicture: "string", cocktailName: "string", recoUser: 0, cocktailDetail: "string" },
    },
    {
      info: { cocktailPicture: "string", cocktailName: "string", recoUser: 1, cocktailDetail: "string" },
    },
    {
      info: { cocktailPicture: "string", cocktailName: "string", recoUser: 2, cocktailDetail: "string" },
    },
  ];

  return (
    <StyledForm
      onSubmit={(e) => {
        e.preventDefault();
        const inputValues = [
          barName,
          barLocation,
          barLocationDetail,
          barMood,
          activeCoverCharge,
          coverCharge,
          activeDiscount,
          discount,
          barOpeningTime,
          barDetail,
          barPics,
          cocktailList,
        ];
        handleSubmit(e, inputValues);
      }}
    >
      <h2 className="a11y-hidden"> </h2>

      <section>
        {/** idx, name */}
        {/** 0.  (barName) */}
        <StyledSectionBarInfo>
          <StyledH3></StyledH3>
          <Input
            {...BAR_INFO.NAME}
            value={barName.replaceAll(" ", "")}
            onChange={(e) => handleChangeInput(e, setBarName)}
          />
        </StyledSectionBarInfo>
        {/** 1, 2.  , (barLocation, barLocationDetail) */}
        <StyledSectionBarInfo>
          <StyledH3></StyledH3>
          <SelectBox
            styletype="secondary"
            selected={barLocation}
            setSelected={function (value: React.SetStateAction<string>): void {
              setBarLocation(value);
            }}
            data={["# ", "# ", "# "]}
            placeholder={""}
            nulltext={""}
          ></SelectBox>
          <Input
            {...BAR_INFO.LOCATION}
            value={barLocationDetail}
            onChange={(e) => setBarLocationDetail(e.target.value)}
          />
        </StyledSectionBarInfo>
        {/** 3.  (barMood) */}
        <StyledSectionBarInfo>
          <StyledH3></StyledH3>
          <SelectBox
            styletype="secondary"
            selected={barMood}
            setSelected={function (value: React.SetStateAction<string>): void {
              setBarMood(value);
            }}
            data={["#", "#", "#"]}
            placeholder={""}
            nulltext={""}
          ></SelectBox>
        </StyledSectionBarInfo>
        {/** 4, 5. (activeCoverCharge, coverCharge) */}
        <StyledSectionBarInfo>
          <StyledH3></StyledH3>
          <SelectBox
            styletype="secondary"
            selected={activeCoverCharge}
            setSelected={function (value: React.SetStateAction<string>): void {
              setActiveCoverCharge(value);
            }}
            data={["", ""]}
            placeholder={""}
            nulltext={""}
          ></SelectBox>
          <Input
            {...BAR_INFO.COVER_CHARGE}
            value={coverCharge}
            onChange={(e) => handleChangeInputNumber(e, setCoverCharge)}
          />
        </StyledSectionBarInfo>
        {/** 6, 7.  (activeDicount, discount) */}
        <StyledSectionBarInfo>
          <StyledH3></StyledH3>
          <SelectBox
            styletype="secondary"
            selected={activeDiscount}
            setSelected={function (value: React.SetStateAction<string>): void {
              setActiveDiscount(value);
            }}
            data={["", ""]}
            placeholder={""}
            nulltext={""}
          ></SelectBox>
          <Input {...BAR_INFO.DISCOUNT} value={discount} onChange={(e) => handleChangeInputNumber(e, setDiscount)} />
        </StyledSectionBarInfo>
      </section>
      <section>
        {/** 8.  (barOpeningTime) */}
        <StyledH3>     </StyledH3>
        <StyledSectionsBarDesc>
          <StyledTextarea
            value={barOpeningTime}
            onChange={(e) => setBarOpeningTime(e.target.value)}
            placeholder="  Bar      
  "
          ></StyledTextarea>
          <StyledSpan>
            {`Ex. - 17:00-24:00
            - 15:00-24:00
          `}
          </StyledSpan>
        </StyledSectionsBarDesc>
        {/** 9.  (barDetail) */}
        <StyledSectionsBarDesc>
          <StyledH3> </StyledH3>
          <StyledTextarea
            value={barDetail}
            onChange={(e) => setBarDetail(e.target.value)}
            placeholder="    . ( 50)"
          ></StyledTextarea>
          <StyledSpan>
            {`Ex.     .    50  .    ,   ?  
          `}
          </StyledSpan>
        </StyledSectionsBarDesc>
        {/** 10.  (barPics) */}
        <StyledSectionsBarDesc>
          <StyledH3> </StyledH3>
          <StyledP>
            {`1)   2)   3)  
              4)       
            `}
            <span>{"(   1:1 )"}</span>
          </StyledP>
          <PhotoList>
            {previewImageList.map((item, idx) => {
              return (
                <li key={`barImage_${idx}`}>
                  <button type="button" value={idx} onClick={deletePreviewImage}>
                    <svg width={10} height={10}>
                      <line x1={1} y1={4.5} x2={9} y2={4.5} style={{ stroke: "black", strokeWidth: "2" }} />
                    </svg>
                  </button>
                  <img src={item} alt={`${idx + 1}  `} />
                </li>
              );
            })}
            {previewImageList.length < 4 && (
              <li key="item_1">
                <input
                  type="file"
                  onChange={handleBarPics}
                  accept="image/*"
                  id="image_1"
                  multiple
                  style={{ display: "none" }}
                />
                <label htmlFor="image_1">
                  <img src={sampleImg} alt="" />
                </label>
              </li>
            )}
          </PhotoList>
        </StyledSectionsBarDesc>
      </section>
      <section>
        <StyledH3>  &#40; 5&#41;</StyledH3>
        <CocktailList>
          {sampleCocktails.map((item, idx) => {
            return (
              <CocktailItem key={`key_${idx}`} id={`cocktail_${idx}`} info={item.info} setShowList={setShowList} />
            );
          })}
        </CocktailList>
        <AddCocktailButton>
          <img src={addCocktailImg} alt="" />
        </AddCocktailButton>
      </section>

      <Button {...ButtonOptions} onClick={() => {}}></Button>
    </StyledForm>
  );
};

const StyledForm = styled.form`
  & > button {
    margin-top: 50px;
  }
`;

const StyledH3 = styled.h3`
  min-width: 60px;
  font-family: var(--font--Bold);
  font-size: 0.875rem;
  text-align: left;
  letter-spacing: 0px;
`;

const StyledSectionBarInfo = styled.section`
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin-bottom: 30px;

  & > input {
    flex-basis: 100%;
    font-family: var(--font--Medium);
    letter-spacing: -0.5px;
  }

  &:nth-of-type(2) > div {
    flex-basis: 96px;
    flex-shrink: 0;
  }
  &:nth-of-type(3) > div {
    flex-grow: 1;
  }
  &:nth-of-type(4),
  &:nth-of-type(5) {
    & > div {
      flex-basis: 77px;
      flex-shrink: 0;
    }
  }

  &:nth-of-type(5) {
    ${StyledH3} {
      position: relative;
      top: -10px;

      &::before {
        content: "";
        position: absolute;
        top: 15px;
        left: 0;
      }

      &::after {
        content: "";
        position: absolute;
        top: 15px;
        right: 15px;
      }
    }

    & > input {
      font-size: 0.75rem;
    }
  }
`;

const StyledSpan = styled.span`
  position: absolute;
  top: 75px;
  left: 10px;
  font-family: var(--font--Medium);
  font-size: 0.8125rem;
  color: var(--gray300-color);
  white-space: pre-line;
  padding-right: 10px;
`;

const StyledSectionsBarDesc = styled.section`
  position: relative;
`;

const StyledTextarea = styled.textarea`
  position: relative;
  width: 100%;
  height: 120px;
  margin: 20px 0 30px;
  padding: 10px;
  border: 1px solid var(--gray200-color);
  border-radius: 6px;
  box-sizing: border-box;
  resize: none;
  font-size: 0.8125rem;
  letter-spacing: -0.7px;

  &:hover,
  &:focus {
    outline: 1px solid var(--main-color);
  }

  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-thumb {
    width: 4px;
    border-radius: 2px;
    background-color: var(--gray200-color);
  }

  &:not(:placeholder-shown) + span {
    display: none;
  }
`;

const StyledP = styled.p`
  margin: 10px 0;
  font-family: var(--font--Medium);
  font-size: 0.8125rem;
  line-height: 1rem;
  color: var(--gray400-color);
  white-space: pre-line;

  & > span {
    color: var(--gray300-color);
  }
`;

const PhotoList = styled.ul`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
  margin: 20px 0;

  & li {
    position: relative;
    width: 70px;
    height: 70px;
    border: 1px solid var(--gray200-color);
    border-radius: 6px;
    overflow: hidden;
  }

  & li > button {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: white;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    text-align: center;
    cursor: pointer;

    & > img {
      width: 8px;
    }
  }

  & label {
    display: block;
    width: 70px;
    height: 70px;
    object-fit: contain;
    cursor: pointer;
  }

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
`;

const CocktailList = styled.ul`
  margin: 20px 0;

  & li {
    width: 100%;
    margin-bottom: 15px;
    padding: 8px 10px;
    background-color: var(--gray100-color);
    border-radius: 4px;
    box-sizing: border-box;
    overflow: hidden;

    & > button {
      width: 15px;
      height: 15px;
      cursor: pointer;

      &:first-of-type {
        float: left;
      }

      &:last-of-type {
        float: right;
      }
    }

    & > div {
      margin: 29px 20px 12px;
    }
  }
`;

const AddCocktailButton = styled.button`
  width: 100%;
  height: 45px;
  background-color: white;
  border: 1px solid var(--gray200-color);
  border-radius: 4px;
  text-align: center;
  cursor: pointer;
`;
