import React, { useState, useEffect } from "react";
import Layout from "../../layouts/Layout";
import Input from "../../components/common/input/Input";
import Category from "../../components/search/Category";
import Item from "../../components/common/item/Item";
import handleSubmit from "./handleSubmit";
import getBarListHome from "./initBarList";
import searchIcon from "../../assets/icon/icon_search.svg";
import NewTag from "../../components/tag/NewTag";
import { CategoryProps, InputProps } from "../../libs/interface/interfaceCommon";
import { FORM_EVENT, INPUT_EVENT } from "../../libs/interface/typeEvent";
import { BarProps } from "../../libs/interface/interfaceBarDetail";
import { styled } from "styled-components";
import { SearchCategoryType } from "../../libs/interface/interfaceSearch";
import { CocktailProps } from "../../libs/interface/interfaceCocktail";

const Search = () => {
  const [inputValue, setInputValue] = useState("");
  const [category, setCategory] = useState<SearchCategoryType>("barName");
  const [tag, setTag] = useState("");
  const [searchData, setSearchData] = useState<Array<BarProps | CocktailProps>>([]);
  const [filteringData, setFilteringData] = useState<Array<BarProps | CocktailProps>>([]);

  useEffect(() => {
    //    
    const initRandomBar = async () => {
      const response = await getBarListHome();
      const randomBarList = response?.data as BarProps[];

      setSearchData(randomBarList);
    };

    initRandomBar();
  }, []);

  useEffect(() => {
    //     
    let filteringItem: Array<BarProps | CocktailProps> = [];

    if (category === "cocktail") {
      filteringItem = searchData.filter((item: any) => {
        return item.recoUser === tag;
      }) as CocktailProps[];
      setFilteringData([...filteringItem]);
    } else {
      filteringItem = searchData.filter((item: any) => {
        return item.barMood === tag;
      }) as BarProps[];
      setFilteringData([...filteringItem]);
    }
  }, [tag]);

  const handleSearch = (e: INPUT_EVENT) => {
    setInputValue(e.target.value);
  };

  const handleCategory = (e: INPUT_EVENT) => {
    setCategory(e.target.value as SearchCategoryType);
  };

  const inputOptions: InputProps = {
    typevariants: "search",
    sizevariants: "medium",
    value: inputValue,
    type: "text",
    placeholder: "  Bar  ?",
    onChange: handleSearch,
  };

  const categoryList = [
    ["", "barName"],
    ["", "cocktail"],
    ["", "barMood"],
    ["", "barLocation"],
  ];
  const barTagOptions = [
    [0, ""],
    [1, ""],
    [2, ""],
    [3, ""],
    [4, ""],
    [5, ""],
    [6, ""],
    [7, ""],
  ] as [number, string][];

  const cocktailTagOption = [
    [0, "1"],
    [1, "2"],
    [2, "3"],
  ] as [number, string][];

  return (
    <Layout>
      <InputContainer
        onSubmit={async (e: FORM_EVENT) => {
          const response = await handleSubmit(e, inputValue, category);
          setSearchData(response?.data);
        }}
      >
        <StyledTitle>BAR </StyledTitle>
        <Input {...inputOptions} />
        <SearchButton type="submit">
          <img src={searchIcon} alt="" />
        </SearchButton>
      </InputContainer>
      <CategoryContainer>
        <MenuSection>
          {categoryList?.map((item, idx) => {
            const categoryOptions: CategoryProps = {
              menu: item[0],
              value: item[1] as SearchCategoryType,
              idx: idx,
              onChange: handleCategory,
            };

            return <Category {...categoryOptions} key={idx} />;
          })}
        </MenuSection>
        <TagSection>
          <NewTag
            typevariants="primary"
            itemlist={category === "cocktail" ? cocktailTagOption : barTagOptions}
            settag={setTag}
          />
        </TagSection>
      </CategoryContainer>
      {category === "cocktail" ? (
        <ListContainer>
          {!filteringData ? (
            <EmptyList key={"emptyList"}>" ."</EmptyList>
          ) : (
            filteringData.map((item, idx) => {
              const data: any = item;
              return (
                <Item key={`search_item_${idx}`} typevariants={"primary"} link={""} url={""} name={data.cocktailName} />
              );
            })
          )}
        </ListContainer>
      ) : (
        <ListContainer>
          {tag ? (
            !filteringData ? (
              <EmptyList key={"emptyList"}>" ."</EmptyList>
            ) : (
              filteringData.map((item, idx) => {
                const data: any = item;
                return (
                  <Item key={`search_item_${idx}`} typevariants={"primary"} link={""} url={""} name={data.barName} />
                );
              })
            )
          ) : !searchData ? (
            <EmptyList key={"emptyList"}>" ."</EmptyList>
          ) : (
            searchData.map((item, idx) => {
              const data: any = item;
              return (
                <Item key={`search_item_${idx}`} typevariants={"primary"} link={""} url={""} name={data.barName} />
              );
            })
          )}
        </ListContainer>
      )}
    </Layout>
  );
};

export default Search;

const StyledTitle = styled.h1`
  margin-bottom: 27px;
  font-family: var(--font--Medium);
  line-height: 140%;
  font-size: 20px;
  color: white;
`;

const InputContainer = styled.form`
  width: 100%;
  height: 145px;
  padding: 23px 20px 21px;
  box-sizing: border-box;
  background-color: var(--main-color);

  & > input {
    margin: 0 auto;
  }
`;

const CategoryContainer = styled.section``;

const MenuSection = styled.section`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  margin-top: 12px;
  padding: 20px;
`;

const TagSection = styled.section`
  position: relative;

  & > ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
    padding: 0 20px;
  }

  & > ul > li {
    margin-right: 10px;
  }
`;

const ListContainer = styled.ul`
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px 10px;
  padding: 20px;
`;

const SearchButton = styled.button`
  position: absolute;
  top: 81px;
  right: 15px;
  width: 40px;
  height: 40px;
  cursor: pointer;
`;

const EmptyList = styled.span`
  font-size: 20px;
`;
